import React, { useEffect, useState } from "react";
import ScrollToBottom from "react-scroll-to-bottom";
import { Alert,AlertTitle } from "@mui/material";
//初始化資料
//資料存在網頁上
//改對應人名，加密方法

function Chat({socket,username,room,otheruser}) {
    // try {
    //     JSON.parse(localStorage.getItem(username))["data"].map(()=>{return 0});
    // } catch(error){
    //     localStorage.setItem(username,JSON.stringify({"data" : []}));//初始化
    // }
    // const [join,setJoin] = useState(false);

    const [preMessageList, setPreMessageList] = useState([]);
    const [messageList, setMessageList] = useState([]);
    const [currentMessage, setCurrentMessage] = useState("");
    const [alert,setAlert] = useState(<div></div>);
    useEffect(()=>{
        socket.emit("init_set",{user:username,room:room});
    },[])

    const sendMessage = async ()=>{
        if (currentMessage !== ""){
            const messageData = {
                room: room,
                author: username,
                message: currentMessage,
                time: new Date(Date.now()).getHours() + 
                ":" +
                new Date(Date.now()).getMinutes()
            };

            await socket.emit("send_message",messageData);

            setMessageList((list) => [...list,messageData]);

            setCurrentMessage("");
        }
    };

    useEffect(() => {
        socket.on("receive_message",(data)=>{
            if(username!==data.author){
                setMessageList((list)=>[...list,data]);
            }
        });
        socket.on("initial_message",(data)=>{
            setPreMessageList(data);
        })
    }, [socket,username]);
    
    // 不重複傳送
    // useEffect(()=>{
    //     console.log(messageList, messageList.length);
    //     if (messageList.length > 1){
    //         let data = messageList;
    //         for(var i = 0; i<messageList.length;i++){
    //             let dataA = messageList[messageList.length-1];
    //             let dataB = messageList[messageList.length-2];
    //             console.log(dataA,dataB);
    //             if(dataA['message'] === dataB['message'] && dataA['author'] === dataB['author'] && dataA['time'] === dataB['time']){
    //                 data.pop(data.length-1);
    //             }
    //             else break;
    //         setMessageList(data);
    //         console.log(data);
    //     }}
    // },[messageList])

    var pattern = new RegExp("[A-Za-z]+");//只傳英文
    return (
            <div className="chat-window">
                <div className="chat-header">
                    <p>{otheruser}</p>
                </div>
                <div className="chat-body">
                    <ScrollToBottom className="message-container">
                    {preMessageList.map((messageContent) => {
                        return (
                        <div className="message" id = {username === messageContent.author?"you" : "other"}>
                            <div >
                                <div className="message-content">
                                    <p>{messageContent.message}</p>
                                </div>
                                <div className="message-meta">
                                    <p id = "time">{messageContent.time}</p>
                                    <p id = "author">{messageContent.author}</p>
                                </div>
                            </div>
                        </div>);
                    })}
                    {messageList.map((messageContent) => {
                        return (
                        <div className="message" id = {username === messageContent.author?"you" : "other"}>
                            <div >
                                <div className="message-content">
                                    <p>{messageContent.message}</p>
                                </div>
                                <div className="message-meta">
                                    <p id = "time">{messageContent.time}</p>
                                    <p id = "author">{messageContent.author}</p>
                                </div>
                            </div>
                        </div>);
                    })}
                    </ScrollToBottom>
                </div>
                <div className="chat-footer">
                    <input
                        type = "text"
                        value={currentMessage}
                        placeholder="Hey..."
                        onChange = {(event) => {
                            for(let i=0;i<event.target.value.length;i++){
                                if (!pattern.test(event.target.value[i])&&event.target.value[i]!==' ') {
                                    event.target.value = '';
                                    setAlert(
                                        <Alert severity="info" className="Alert">
                                            <AlertTitle>Info</AlertTitle>
                                            <p>Please type letters only!</p>
                                            <p>只能輸入英文字母</p>
                                            <strong>check it out!</strong>
                                        </Alert>)
                                    setTimeout(()=>{setAlert(<div></div>)},5000);
                                    break;
                                }
                            }
                            setCurrentMessage(event.target.value);
                        }}
                        onKeyPress = {(event) => {event.key === "Enter" && sendMessage()}}/>
                    <button onClick={sendMessage}> &#9658; </button>
                    {alert}
                </div>
            </div>
    );
}
export default Chat;