import './style/Drawer.css';
import * as React from 'react';
import { useState } from 'react';
import { Link } from 'react-router-dom';
import Drawer from '@mui/material/Drawer'; 
import Box from '@mui/material/Box';
import List from '@mui/material/List';
import Divider from '@mui/material/Divider';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemText from '@mui/material/ListItemText';


function MyDrawer(props) {
    
    const [draw,setDraw] = useState(false);
    const toggleDrawer = (open) => (event) => {
      if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
        return;
      }
      setDraw(open);
    };
    const list = props.Login?(
      <Box
        sx= {{width:250}} 
        role="presentation"
        onClick={toggleDrawer(false)}
        onKeyDown={toggleDrawer(false)}
      >
        <List>
          {/* {['聊天室', '控制台','設定'].map((text) => (
            <ListItem key={text} disablePadding>
              <ListItemButton LinkComponent={Link} to='/'>
                <ListItemText primary={text} />
              </ListItemButton>
            </ListItem>
          ))} */}
          <ListItem key={'聊天室'} disablePadding>
            <ListItemButton LinkComponent={Link} to='/sixpin00/oldglx/Cho'>
              <ListItemText primary={'聊天室'} />
            </ListItemButton>
          </ListItem>
          <ListItem key={'診療室'} disablePadding>
            <ListItemButton LinkComponent={Link} to='/sixpin00/oldglx/Pri'>
              <ListItemText primary={'診療室'} />
            </ListItemButton>
          </ListItem>
          <ListItem key={'公用電腦'} disablePadding>
            <ListItemButton LinkComponent={Link} to='/sixpin00/oldglx/Pub'>
              <ListItemText primary={'公用電腦'} />
            </ListItemButton>
          </ListItem>
          <ListItem key={'控制台'} disablePadding>
            <ListItemButton LinkComponent={Link} to='/sixpin00/oldglx/Lock'>
              <ListItemText primary={'控制台'} />
            </ListItemButton>
          </ListItem>
          <ListItem key={'設定'} disablePadding>
            <ListItemButton LinkComponent={Link} to='/sixpin00/oldglx/Info'>
              <ListItemText primary={'設定'} />
            </ListItemButton>
          </ListItem>
        </List>
        <Divider />
      </Box>
    ):(
      <Box
        sx= {{width:250}} 
        role="presentation"
        onClick={toggleDrawer(false)}
        onKeyDown={toggleDrawer(false)}
      >
        <List>
          {/* {['登入','關於'].map((text) => (
            <ListItem key={text} disablePadding>
              <ListItemButton LinkComponent={Link} to='/'>
                <ListItemText primary={text} />
              </ListItemButton>
            </ListItem>
          ))} */}
          <ListItem key={'登入'} disablePadding>
              <ListItemButton LinkComponent={Link} to='/sixpin00/Login'>
                <ListItemText primary={'登入'} />
              </ListItemButton>
            </ListItem>
          <ListItem key={'關於'} disablePadding>
            <ListItemButton LinkComponent={Link} to='/about'>
              <ListItemText primary={'關於'} />
            </ListItemButton>
          </ListItem>
        </List>
        <Divider />
      </Box>
    );

  
    return (
      <header>
        <nav>
          <div className='toolbar'>
            {/* <h2>醫院控制台</h2>
            <Switch/> Lock the Door  */}
            {props.Login?(<div><a href = '/sixpin00/oldglx'>臺北市立松江醫院,Taipei City Songjiang Hospital</a></div>):(<div><a href = '/sixpin00'>臺北市立松江醫院</a></div>)}
             <div>
                <button onClick={toggleDrawer(true)}>
                  <div className='button__line'></div>
                  <div className='button__line'></div>
                  <div className='button__line'></div>
                </button>
              </div>
                <Drawer
                  anchor='right'
                  open={draw}
                  onClose={toggleDrawer(false)}
                >{list}
                </Drawer> 
            
          </div> 
        </nav>
        <div className='footer'>
          <p>本網站為cycle密室逃脫遊戲所設立</p>
        </div>
      </header>     
    );
}

export default MyDrawer;
